.c-Reports {
    width: 80%;
    max-width: 800px;
    margin: auto;

    &--pathLinks{
        text-align: left;
        margin: 20px 0;

        a {
            color: #0091ea;
        }
    }
}

.c-Reports ul {
    list-style: none;
    margin: 0;
    padding: 0;
    text-align: left;
}

.c-Reports ul li {
    padding: 8px 16px;
    margin-bottom: 8px;
    background: rgba(129, 199, 132, .6);
}

.c-Reports ul li.report-suite-error {
    background: rgba(255, 138, 101, .6);
}

.c-Reports ul li:hover {
    cursor: pointer;
    background: rgba(149, 219, 152, .6);
}

.c-Reports ul li.report-suite-error:hover {
    background: rgba(255, 158, 121, .6);
}

.c-Reports ul li {
    display: flex;
    flex-direction: row;
}

.c-Reports ul li>* {
    flex: 1;
}

.c-Reports ul li>.counter-stats {
    max-width: 50px;
    text-align: right;
}

.c-Reports .report-suite-has-message {
    margin-bottom: 0;
    border-bottom: thin solid #ccc;
}

