.AnalyticsDashboard {
    text-align: center;
    height: 100%;
    overflow: auto;
}

.AnalyticsDashboard>div {
    height: 100%;
}

.AnalyticsDashboard-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .AnalyticsDashboard-logo {
        animation: AnalyticsDashboard-logo-spin infinite 20s linear;
    }
}

.AnalyticsDashboard-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.AnalyticsDashboard-link {
    color: #61dafb;
}

@keyframes AnalyticsDashboard-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.material-icons {
    vertical-align: middle;
}